          .replace(/<code[^>]*>(.*?)<\/code>/gi, '`$1`')
          // é“¾æ¥
          .replace(/<a[^>]*href="([^"]*)"[^>]*>(.*?)<\/a>/gi, '[$2]($1)')
          // å›¾ç‰‡
          .replace(/<img[^>]*src="([^"]*)"[^>]*alt="([^"]*)"[^>]*>/gi, '![$2]($1)')
          .replace(/<img[^>]*alt="([^"]*)"[^>]*src="([^"]*)"[^>]*>/gi, '![$1]($2)')
          .replace(/<img[^>]*src="([^"]*)"[^>]*>/gi, '![]($1)')
          // ä»£ç å—
          .replace(/<pre[^>]*><code[^>]*>([\s\S]*?)<\/code><\/pre>/gi, '```\n$1\n```')
          // åˆ—è¡¨é¡¹
          .replace(/<li[^>]*>(.*?)<\/li>/gi, '- $1')
          // å¼•ç”¨
          .replace(/<blockquote[^>]*>(.*?)<\/blockquote>/gi, '> $1')
          // æ°´å¹³çº¿
          .replace(/<hr[^>]*>/gi, '---')
          // æ®µè½
          .replace(/<p[^>]*>(.*?)<\/p>/gi, '$1\n\n')
          // æ¢è¡Œ
          .replace(/<br[^>]*>/gi, '\n')
          // ç§»é™¤å…¶ä»–HTMLæ ‡ç­¾
          .replace(/<[^>]+>/g, '')
          // æ¸…ç†å¤šä½™çš„ç©ºè¡Œ
          .replace(/\n{3,}/g, '\n\n')
          .trim()

        this.outputText = markdown
        this.highlightedOutput = this.highlightMarkdown(markdown)
      } catch (error) {
        this.outputText = 'è½¬æ¢é”™è¯¯: ' + error.message
