---

è¿™å°±æ˜¯Markdownçš„åŸºæœ¬è¯­æ³•ç¤ºä¾‹ï¼`
      } else {
        this.inputText = `<!DOCTYPE html>
<html>
<head>
    <title>HTMLç¤ºä¾‹</title>
</head>
<body>
    <h1>HTMLç¤ºä¾‹æ–‡æ¡£</h1>
    
    <h2>æ–‡æœ¬æ ·å¼</h2>
    <p>è¿™æ˜¯ä¸€ä¸ª<strong>ç²—ä½“æ–‡æœ¬</strong>å’Œ<em>æ–œä½“æ–‡æœ¬</em>çš„ç¤ºä¾‹ã€‚</p>
    <p>ä½ ä¹Ÿå¯ä»¥ä½¿ç”¨<del>åˆ é™¤çº¿</del>å’Œ<code>è¡Œå†…ä»£ç </code>ã€‚</p>
    
    <h2>åˆ—è¡¨</h2>
    <ul>
        <li>æ— åºåˆ—è¡¨é¡¹1</li>
        <li>æ— åºåˆ—è¡¨é¡¹2</li>
    </ul>
    
    <ol>
        <li>æœ‰åºåˆ—è¡¨é¡¹1</li>
        <li>æœ‰åºåˆ—è¡¨é¡¹2</li>
    </ol>
    
    <h2>é“¾æ¥å’Œå›¾ç‰‡</h2>
    <p>è¿™æ˜¯ä¸€ä¸ª<a href="https://example.com">é“¾æ¥ç¤ºä¾‹</a>ã€‚</p>
    <img src="https://via.placeholder.com/150" alt="å›¾ç‰‡ç¤ºä¾‹">
    
    <h2>ä»£ç å—</h2>
    <pre><code>function hello() {
    console.log("Hello, World!");
}</code></pre>
    
    <h2>å¼•ç”¨</h2>
    <blockquote>è¿™æ˜¯ä¸€ä¸ªå¼•ç”¨ç¤ºä¾‹ã€‚</blockquote>
    
