/**
 * ä¸‹è½½å·¥å…·ç±»
 * å°è£…å„ç§ä¸‹è½½åŠŸèƒ½ï¼Œé¿å…é‡å¤ä»£ç 
 */
export class DownloadUtil {
    /**
     * ä¸‹è½½æ–‡æœ¬æ–‡ä»¶
     * @param {string} content - æ–‡ä»¶å†…å®¹
     * @param {string} filename - æ–‡ä»¶å
     * @param {string} mimeType - MIMEç±»å‹
     */
    static downloadText(content, filename, mimeType = 'text/plain') {
        const blob = new Blob([content], { type: mimeType })
        this.downloadBlob(blob, filename)
    }

    /**
     * ä¸‹è½½JSONæ–‡ä»¶
     * @param {Object} data - JSONæ•°æ®
     * @param {string} filename - æ–‡ä»¶å
     */
    static downloadJSON(data, filename = 'data.json') {
        const content = JSON.stringify(data, null, 2)
        this.downloadText(content, filename, 'application/json')
    }

    /**
     * ä¸‹è½½CSVæ–‡ä»¶
     * @param {Array} data - CSVæ•°æ®æ•°ç»„
     * @param {string} filename - æ–‡ä»¶å
     * @param {Array} headers - è¡¨å¤´æ•°ç»„
     */
    static downloadCSV(data, filename = 'data.csv', headers = null) {
        let csvContent = ''
        
        // æ·»åŠ è¡¨å¤´
        if (headers && headers.length > 0) {
            csvContent += headers.join(',') + '\n'
        }
        
        // æ·»åŠ æ•°æ®è¡Œ
        data.forEach(row => {
            if (Array.isArray(row)) {
                csvContent += row.map(field => `"${field}"`).join(',') + '\n'
            } else if (typeof row === 'object') {
                const values = headers ? headers.map(header => row[header] || '') : Object.values(row)
                csvContent += values.map(field => `"${field}"`).join(',') + '\n'
            }
        })
        
        // æ·»åŠ BOMä»¥æ”¯æŒä¸­æ–‡
        const BOM = '\uFEFF'
        this.downloadText(BOM + csvContent, filename, 'text/csv;charset=utf-8')
    }

    /**
     * ä¸‹è½½å›¾ç‰‡æ–‡ä»¶
     * @param {string} dataURL - å›¾ç‰‡DataURL
     * @param {string} filename - æ–‡ä»¶å
     */
    static downloadImage(dataURL, filename = 'image.png') {
        const link = document.createElement('a')
        link.download = filename
        link.href = dataURL
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

    /**
     * ä¸‹è½½Canvasä¸ºå›¾ç‰‡
     * @param {HTMLCanvasElement} canvas - Canvaså…ƒç´ 
     * @param {string} filename - æ–‡ä»¶å
     * @param {string} format - å›¾ç‰‡æ ¼å¼
     * @param {number} quality - å›¾ç‰‡è´¨é‡(0-1)
     */
    static downloadCanvas(canvas, filename = 'canvas.png', format = 'image/png', quality = 1.0) {
        const dataURL = canvas.toDataURL(format, quality)
        this.downloadImage(dataURL, filename)
    }

    /**
     * ä¸‹è½½Blobå¯¹è±¡
     * @param {Blob} blob - Blobå¯¹è±¡
     * @param {string} filename - æ–‡ä»¶å
     */
    static downloadBlob(blob, filename) {
        const url = URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = url
        link.download = filename
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
    }

    /**
     * ä¸‹è½½URLæŒ‡å‘çš„æ–‡ä»¶
     * @param {string} url - æ–‡ä»¶URL
     * @param {string} filename - æ–‡ä»¶å
     */
    static downloadURL(url, filename) {
        const link = document.createElement('a')
        link.href = url
        link.download = filename
        link.target = '_blank'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

    /**
     * æ‰¹é‡ä¸‹è½½æ–‡ä»¶ä¸ºZIP
     * @param {Array} files - æ–‡ä»¶æ•°ç»„ [{name: string, content: string|Blob}]
     * @param {string} zipName - ZIPæ–‡ä»¶å
     */
    static async downloadZip(files, zipName = 'files.zip') {
        // åŠ¨æ€å¯¼å…¥JSZip
        const JSZip = (await import('jszip')).default
        const zip = new JSZip()

        // æ·»åŠ æ–‡ä»¶åˆ°ZIP
        files.forEach(file => {
            if (file.content instanceof Blob) {
                zip.file(file.name, file.content)
            } else {
                zip.file(file.name, file.content)
            }
        })

        // ç”ŸæˆZIPå¹¶ä¸‹è½½
        try {
            const content = await zip.generateAsync({ type: 'blob' })
            this.downloadBlob(content, zipName)
            return true
        } catch (error) {
            console.error('ç”ŸæˆZIPæ–‡ä»¶å¤±è´¥:', error)
            return false
        }
    }

    /**
     * ä»URLè·å–Blob
     * @param {string} url - æ–‡ä»¶URL
     * @returns {Promise<Blob>} Blobå¯¹è±¡
     */
    static async fetchBlob(url) {
        try {
            const response = await fetch(url)
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`)
            }
            return await response.blob()
        } catch (error) {
            console.error('è·å–æ–‡ä»¶å¤±è´¥:', error)
            throw error
        }
    }

    /**
     * ç”Ÿæˆå¸¦æ—¶é—´æˆ³çš„æ–‡ä»¶å
     * @param {string} baseName - åŸºç¡€æ–‡ä»¶å
     * @param {string} extension - æ–‡ä»¶æ‰©å±•å
     * @returns {string} å¸¦æ—¶é—´æˆ³çš„æ–‡ä»¶å
     */
    static generateTimestampFilename(baseName, extension) {
        const timestamp = new Date().toISOString().replace(/[:.]/g, '-').slice(0, -5)
        return `${baseName}_${timestamp}.${extension}`
    }

    /**
     * è·å–æ–‡ä»¶å¤§å°çš„å¯è¯»æ ¼å¼
     * @param {number} bytes - å­—èŠ‚æ•°
     * @returns {string} æ ¼å¼åŒ–çš„æ–‡ä»¶å¤§å°
     */
    static formatFileSize(bytes) {
        if (bytes === 0) return '0 Bytes'
        
        const k = 1024
        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
        const i = Math.floor(Math.log(bytes) / Math.log(k))
        
        return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
}

export default DownloadUtil
